"use client"

import { useEffect, useState, useCallback } from "react"
import { useRouter } from "next/navigation"
import Script from "next/script"
import Image from "next/image"
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { Loader2, ShoppingBag, Clock, Timer, Calendar } from "lucide-react"
import logo from "@/lib/images/image copy.png"

// ÌÉÄÏûÖ Ï†ïÏùò
interface Product {
  productId: string;
  advisorId: string;
  productName: string;
  price: number;
  startTime: number;
  endTime: number;
  durationMin: number;
  category: string;
  description: string;
  dayOfWeek: string;
}

export default function DashboardPage() {
  const router = useRouter()
  const [products, setProducts] = useState<Product[]>([])
  const [isLoading, setIsLoading] = useState(true)

  // üí° SDK Î°úÎî© ÏÉÅÌÉú Í¥ÄÎ¶¨
  const [tossLoaded, setTossLoaded] = useState(false)

  // Îç∞Ïù¥ÌÑ∞ Î°úÎî© (Í∏∞Ï°¥ Î°úÏßÅ Ïú†ÏßÄ)
  useEffect(() => {
    const token = localStorage.getItem("accessToken")
    fetch("http://34.50.7.8:30000/v1/products", {
      headers: { "Authorization": `Bearer ${token}` }
    })
    .then(res => res.json())
    .then(data => {
      setProducts(data.result || [])
      setIsLoading(false)
    })
  }, [])

  // --- [ÌïµÏã¨: ÏÉÅÎã¥ Ïã†Ï≤≠ÌïòÍ∏∞ ÌÅ¥Î¶≠ Ïãú Ïã§ÌñâÎêòÎäî Í≤∞Ï†ú Î°úÏßÅ] ---
  const handlePayment = async (product: Product) => {
    const TOSS_CLIENT_KEY = "test_ck_Gv6LjeKD8azXjngMeXkN3wYxAdXy"
    const API_INIT_URL = "http://34.50.7.8:30000/v1/payments/init"
    const SUCCESS_URL = `${window.location.origin}/dashboard/payment/success`
    const FAIL_URL = `${window.location.origin}/dashboard/payment/fail`

    const token = localStorage.getItem("accessToken")
    const userId = localStorage.getItem("userId")

    // 1. SDK Ï°¥Ïû¨ Ïó¨Î∂Ä ÌôïÏù∏
    if (!(window as any).TossPayments) {
      alert("Í≤∞Ï†ú Î™®ÎìàÏù¥ ÏïÑÏßÅ Î°úÎìúÎêòÏßÄ ÏïäÏïòÏäµÎãàÎã§. Ïû†ÏãúÎßå Í∏∞Îã§Î†§Ï£ºÏÑ∏Ïöî.")
      return
    }

    try {
      // 2. Î∞±ÏóîÎìú /init ÏóîÎìúÌè¨Ïù∏Ìä∏ Ìò∏Ï∂ú (ÎèôÏ†Å Îç∞Ïù¥ÌÑ∞ Ï†ÑÏÜ°)
      const response = await fetch(API_INIT_URL, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': "Bearer " + token
        },
        body: JSON.stringify({
          userId: userId,                // Î°úÍ∑∏Ïù∏Ìïú Ïú†Ï†Ä ÏïÑÏù¥Îîî
          productName: product.productName, // Ï†ÑÎ¨∏Í∞Ä ÏÉÅÌíàÎ™Ö
          amount: product.price             // Í∞ÄÍ≤©
        })
      });

      if (!response.ok) throw new Error("Í≤∞Ï†ú Ï¥àÍ∏∞Ìôî Ïã§Ìå®")
      const initData = await response.json()

      // 3. ÌÜ†Ïä§ÌéòÏù¥Î®ºÏ∏† Í∏∞Î≥∏ Í≤∞Ï†úÏ∞Ω Ìò∏Ï∂ú (ÏöîÏ≤≠ÌïòÏã† ÌòïÏãù)
      const tossPayments = (window as any).TossPayments(TOSS_CLIENT_KEY);

      tossPayments.requestPayment("Ïπ¥Îìú", {
        amount: initData.amount,
        orderId: initData.orderId,
        orderName: initData.productName,
        successUrl: SUCCESS_URL,
        failUrl: FAIL_URL,
        metadata: {
          productId: product.productId,
          advisorId: product.advisorId,
          date: '2025-12-18', // ÏòàÏãú ÎÇ†Ïßú
          time: product.startTime.toString(),
          endTime: product.endTime.toString()
        }
      })
      .catch((err: any) => {
        if (err.code === "USER_CANCEL") return;
        console.error("‚ùå Í≤∞Ï†ú ÏöîÏ≤≠ Ïã§Ìå®:", err)
      })

    } catch (error) {
      console.error("Í≤∞Ï†ú ÌîÑÎ°úÏÑ∏Ïä§ ÏóêÎü¨:", error)
      alert("Í≤∞Ï†ú Ï§ÄÎπÑ Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.")
    }
  }

  if (isLoading) return <div className="flex h-screen items-center justify-center"><Loader2 className="animate-spin" /></div>

  return (
    <div className="p-8">
      {/* üí° Ïä§ÌÅ¨Î¶ΩÌä∏ Î°úÎìú - SDK ÏÇ¨Ïö© Ïãú WidgetÏù¥ ÏïÑÎãå Í∏∞Î≥∏ SDK(payment)Î•º Í∂åÏû•Ìï©ÎãàÎã§ */}
      <Script 
        src="https://js.tosspayments.com/v1/payment" 
        onLoad={() => setTossLoaded(true)}
      />
      
      <div className="flex flex-col gap-6">
        <h2 className="text-2xl font-black flex items-center gap-2">
          <ShoppingBag className="text-primary" /> Ï†ÑÎ¨∏Í∞Ä ÏÑúÎπÑÏä§ Ïã†Ï≤≠
        </h2>
        
        <div className="grid grid-cols-1 gap-4">
          {products.map((prod) => (
            <Card key={prod.productId} className="p-6 border-2 hover:border-primary transition-all">
              <div className="flex justify-between items-center">
                <div>
                  <h3 className="text-lg font-bold">{prod.productName}</h3>
                  <p className="text-sm text-slate-500">{prod.description}</p>
                  <div className="flex gap-4 mt-2 text-xs font-bold text-slate-400">
                    <span className="flex items-center gap-1"><Clock className="w-3 h-3"/> {prod.durationMin}Î∂Ñ</span>
                    <span className="flex items-center gap-1"><Timer className="w-3 h-3"/> {prod.startTime} ~ {prod.endTime}</span>
                  </div>
                </div>
                <div className="text-right">
                  <p className="text-xl font-black mb-2">‚Ç©{prod.price.toLocaleString()}</p>
                  <Button 
                    onClick={() => handlePayment(prod)}
                    className="font-bold bg-primary text-white"
                  >
                    ÏÉÅÎã¥ Ïã†Ï≤≠ÌïòÍ∏∞
                  </Button>
                </div>
              </div>
            </Card>
          ))}
        </div>
      </div>
    </div>
  )
}